body {
  background-color: #2c2c2c;
  height: 500px;
  font-family: "Poppins", serif;
}

.container {
  background-color: #2c2c2c;
}

#chart {
  background-color: #2c2c2c;
}

svg.clock {
  stroke-linecap: round;
}

.face {
  stroke: #8a8a8a;
  stroke-width: 6;
}

.hourtick.face {
}

.minutetick.face {
  stroke-width: 2;
}

.hand {
  stroke: #8934d9;
  stroke-width: 8;
}

.hour.hand {
  stroke-width: 12;
}

.minute.hand {
}

.second.hand {
  stroke: #d53c97;
  stroke-width: 4;
}
